//9.
export const addPlate = (data)=> {
  let data2 = data
  let mod = data.moderator
  if(data.moderator<=0){
    mod = 0
  }
  data2={
    plate_name:data.plate_name,
    desciption:data.desciption,
    moderator:mod
  }
  return ajax(`/plates/addplate`,data,'POST')
}

