<template>
  <div>
    <Breadcrumb></Breadcrumb>
    <div>
      <el-row>
        <el-col>
           <el-form :model="edituserinfo"  label-width="100px" class="demo-ruleForm">
              <el-form-item label="">
                {{userDetail.nick_name}}
              </el-form-item>
              <el-form-item label="">
                <el-radio v-model="userRole" label=""></el-radio>
                <el-radio v-model="userRole" label=""></el-radio>
                <el-radio v-model="userRole" label=""></el-radio>
              </el-form-item>
              <el-form-item  v-if="userRole===''">
                <el-select size="small" v-model="edituserinfo.plate_id" placeholder="">
                  <el-option v-for="item in noPlateList" :key="item.plateid" :label="item.plate_name" :value="item.plateid"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-switch v-model="editPassword" active-color="#13ce66" inactive-color="#ff4949"></el-switch>
              </el-form-item>
             <el-col :span="7">
               <el-form-item v-if="editPassword">
                 <el-input size="small" placeholder="" v-model="password1" show-password></el-input>
               </el-form-item>
               <el-form-item v-if="editPassword">
                 <el-input size="small" placeholder="" v-model="password2" show-password></el-input>
               </el-form-item>
               <el-form-item>
                 <el-button size="small" type="primary" @click="submitForm()"></el-button>
                 <el-button size="small" @click="$router.go(-1)"></el-button>
               </el-form-item>
             </el-col>
           </el-form>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import {addUser,reqUserDetail,reqNoPlateList,edituserinfo} from '../../../api/index'
  import {mapState} from 'vuex'
  import Breadcrumb from '../../../components/Breadcrumb/Breadcrumb.vue'
  export default {
    components:{
      Breadcrumb
    },
    data() {
      return {
        userid:"",
        userDetail:{},
        userRole: "",
        selectedUserid:'',
        editPassword:false,
        noPlateList:[],
        password1:'',
        password2:'',
        edituserinfo:{
          role_name:"",
          plate_id:"",
          password:"",
        }
      }
    },
    methods: {
      async init(){
        let result = await reqUserDetail(this.userid)
        this.userDetail = result
        let result2 = await reqNoPlateList(this.userid)
        this.noPlateList = result2
        if(this.userDetail.plate_name!=""){
          this.userRole = ""
          this.edituserinfo.plate_id = this.userDetail.plate_id
        }
      },
      async submitForm(){
        let data = {}
        data.plate_id = this.edituserinfo.plate_id
        data.role_name = this.edituserinfo.role_name
        if(data.role_name==='2'&&data.plate_id==''){
          this.$message.error('');
          return
        }
        if(this.editPassword===true){
          if(this.password1!== this.password2){
            this.$message.error('');
            return
          }else{
            this.edituserinfo.password = this.password1
            data.password = this.password1
          }
          let result3 = await edituserinfo(this.userid,data)
          if(result3.status ===1){
            this.$message({
              message: '',
              type: 'success'
            });
          }
        }else{
          let result4 = await edituserinfo(this.userid,data)
          if(result4.status ===1){
            this.$message({
              message: '',
              type: 'success'
            });
          }
        }
        console.log(data)
      }
    },
    mounted(){
      this.userid = this.$route.params.userid
      this.init()
    },
    computed:{
    },
    watch:{
      edituserinfo(val){
        console.log(this.edituserinfo)
      },
      userRole(val){
        //plate_idNULL
        if(this.userRole === ''){
          this.edituserinfo.role_name = ''
          this.edituserinfo.plate_id = ''
        }else if(this.userRole === ''){
          this.edituserinfo.role_name = ''
          this.edituserinfo.plate_id = ''
        }
        else{
          this.edituserinfo.role_name = ''
        }
      }
    }
  }
</script>

<style>

</style>



